import React, { createContext, useContext, useState, useEffect } from "react";
import api from "../api/api";

const CartContext = createContext(null);
export const useCart = () => useContext(CartContext);

export const CartProvider = ({ children }) => {
  const [cartItems, setCartItems] = useState(() => {
    try {
      const stored = localStorage.getItem("cartItems");
      return stored ? JSON.parse(stored) : [];
    } catch (error) {
      console.error("Error loading cart from localStorage:", error);
      return [];
    }
  });

  // Save cart to localStorage whenever it changes
  useEffect(() => {
    try {
      localStorage.setItem("cartItems", JSON.stringify(cartItems));
    } catch (error) {
      console.error("Error saving cart to localStorage:", error);
    }
  }, [cartItems]);

  // Auto-save to backend with debouncing
  useEffect(() => {
    const saveToBackend = async () => {
      try {
        const customerStr = localStorage.getItem("customer");
        if (!customerStr) {
          console.log("No customer logged in, skipping backend cart save");
          return;
        }

        const customer = JSON.parse(customerStr);
        
        // Validate customerId exists and is valid
        const customerId = Number(customer.customerId);
        if (!customerId || customerId === 0 || isNaN(customerId)) {
          console.warn("Invalid customerId, skipping backend cart save");
          return;
        }

        if (cartItems.length === 0) {
          console.log("Cart is empty, skipping backend cart save");
          return;
        }

        // Build cart payload matching backend expected format
        const cartPayload = {
          customer: { customerId: customerId },
          items: cartItems.map(item => ({
            productId: item.id,
            quantity: item.quantity
          })),
          checkedOut: false
        };

        console.log("💾 Saving cart to backend database:", JSON.stringify(cartPayload, null, 2));

        // Try update first, then create if needed
        try {
          const response = await api.put("/api/cart/update", cartPayload);
          console.log("✅ Cart saved to database (updated):", response.data);
        } catch (updateError) {
          if (updateError.response?.status === 404) {
            const response = await api.post("/api/cart/create", cartPayload);
            console.log("✅ Cart saved to database (created):", response.data);
          } else if (updateError.response?.status === 403) {
            console.warn("⚠️ Cart save blocked by backend authentication. Backend may require authentication token.");
          } else {
            throw updateError;
          }
        }
      } catch (error) {
        if (error.response?.status === 403) {
          console.warn("⚠️ Backend requires authentication for cart operations");
        } else if (error.response?.data?.message?.includes("Duplicate entry")) {
          console.log("Cart already exists for this customer");
        } else {
          console.error("❌ Failed to save cart to backend:", error.response?.data || error.message);
        }
      }
    };

    // Debounce the save operation (wait 1.5 seconds after last cart change)
    const timeoutId = setTimeout(() => {
      saveToBackend();
    }, 1500);

    return () => clearTimeout(timeoutId);
  }, [cartItems]);

  const addToCart = (item) => {
    // Validate item structure
    if (!item.id) {
      console.error("Item missing ID:", item);
      return;
    }
    
    setCartItems((prev) => {
      const existing = prev.find((i) => i.id === item.id);
      
      if (existing) {
        // Item exists, increase quantity
        return prev.map((i) =>
          i.id === item.id ? { ...i, quantity: i.quantity + 1 } : i
        );
      }
      
      // New item, add to cart
      return [...prev, { ...item, quantity: 1 }];
    });
  };

  const removeFromCart = (id) =>
    setCartItems((prev) => prev.filter((i) => i.id !== id));

  const clearCart = () => setCartItems([]);

  const increaseQuantity = (id) =>
    setCartItems((prev) =>
      prev.map((i) =>
        i.id === id ? { ...i, quantity: i.quantity + 1 } : i
      )
    );

  const decreaseQuantity = (id) =>
    setCartItems((prev) =>
      prev
        .map((i) =>
          i.id === id && i.quantity > 1
            ? { ...i, quantity: i.quantity - 1 }
            : i
        )
        .filter((i) => i.quantity > 0)
    );

  return (
    <CartContext.Provider
      value={{
        cartItems,
        addToCart,
        removeFromCart,
        clearCart,
        increaseQuantity,
        decreaseQuantity,
      }}
    >
      {children}
    </CartContext.Provider>
  );
};
